              style="width:71.0500pt;;border-left:none;mso-border-left-alt:none;border-right:1.0000pt solid rgb(0,0,0);mso-border-right-alt:0.5000pt solid rgb(0,0,0);border-top:none;mso-border-top-alt:0.5000pt solid rgb(0,0,0);border-bottom:1.0000pt solid rgb(0,0,0);mso-border-bottom-alt:0.5000pt solid rgb(0,0,0);">

          </td>
          <td width=118 valign=middle
              style="width:71.0500pt;;border-left:none;mso-border-left-alt:none;border-right:1.0000pt solid rgb(0,0,0);mso-border-right-alt:0.5000pt solid rgb(0,0,0);border-top:none;mso-border-top-alt:0.5000pt solid rgb(0,0,0);border-bottom:1.0000pt solid rgb(0,0,0);mso-border-bottom-alt:0.5000pt solid rgb(0,0,0);">

          </td>
        </tr>
        <tr style="height:46.8000pt;">
          <td width=118 valign=middle
              style="width:71.0000pt;;border-left:1.0000pt solid rgb(0,0,0);mso-border-left-alt:0.5000pt solid rgb(0,0,0);border-right:1.0000pt solid rgb(0,0,0);mso-border-right-alt:0.5000pt solid rgb(0,0,0);border-top:none;mso-border-top-alt:0.5000pt solid rgb(0,0,0);border-bottom:1.0000pt solid rgb(0,0,0);mso-border-bottom-alt:0.5000pt solid rgb(0,0,0);">

            <p class=MsoNormal align=center style="text-align:center;"><span
              style="mso-bidi-font-family:'Times New Roman';"></span><span
              style="mso-bidi-font-family:'Times New Roman';"><span></span></span>
            </p>
          </td>
          <td width=591 valign=middle colspan=5
              style="width:355.1000pt;;border-left:none;mso-border-left-alt:none;border-right:1.0000pt solid rgb(0,0,0);mso-border-right-alt:0.5000pt solid rgb(0,0,0);border-top:none;mso-border-top-alt:0.5000pt solid rgb(0,0,0);border-bottom:1.0000pt solid rgb(0,0,0);mso-border-bottom-alt:0.5000pt solid rgb(0,0,0);">
            <p class=MsoNormal style="text-align:left;"><span
              style="mso-bidi-font-family:'Times New Roman';"><span>&nbsp;</span></span>
            </p>
            <p class=MsoNormal style="text-align:left;"><span
              style="mso-bidi-font-family:'Times New Roman';" class="font_class"></span><span
              style="mso-bidi-font-family:'Times New Roman';"><span></span></span>
            </p>
            <p class=MsoNormal style="text-align:left;"><span
              style="mso-bidi-font-family:'Times New Roman';"><span>&nbsp;</span></span>
            </p>
          </td>
        </tr>
      </table>
      <p class=MsoNormal align=center style="text-align:center;"><span
        style="mso-bidi-font-family:'Times New Roman';"><span>&nbsp;</span></span>
      </p>
      <p class=MsoNormal align=center style="text-align:center;"><span
        style="mso-bidi-font-family:'Times New Roman';"><span>&nbsp;</span></span>
      </p>
      <p class=MsoNormal align=center style="text-align:center;"><span
        style="mso-bidi-font-family:'Times New Roman';"><font
        face=""></font></span><span
        style="display: inline-block;width: 150px;border-bottom: 1px solid #000"
        class="font_class"></span><span
        style="mso-bidi-font-family:'Times New Roman';">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <span style="display: inline-block;width: 150px;border-bottom: 1px solid #000"
              class="font_class"></span><span
          style="mso-bidi-font-family:'Times New Roman';"></span><span
          style="mso-bidi-font-family:'Times New Roman';"><span></span></span>
      </p>
      <p class=MsoNormal><span
        style="font-family:Calibri;mso-fareast-mso-bidi-font-family:'Times New Roman';"><span>&nbsp;</span></span>
      </p>
    </div>
    <el-row style="padding: 10px 0;margin: 0;margin-top: 550px;padding-top: 200px">
      <el-col :span="2">
        <!--<p></p>-->
        <el-select v-model="specimen_value" size="mini" placeholder="" @change="specimen_change">
          <el-option
            v-for="item in specimen"
            :key="item.v"
            :label="item.l"
            :value="item.v">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="6">
        <el-row>
          <!--<p></p>-->
          <el-col :span="18">
            <el-input size="mini" v-model="size_type" placeholder=",100110150"></el-input>
          </el-col>
          <el-col :span="6">
            <el-button size="mini" @click="jhl_C" style="width: 100%">
              
            </el-button>
          </el-col>
        </el-row>

      </el-col>
      <el-col :span="4">
        <!--<p></p>-->
        <el-date-picker
          size="mini"
          value-format="yyyy / MM / dd"
          v-model="date_val"
          align="right"
          type="date"
          placeholder="">
        </el-date-picker>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" size="mini" @click="sjsf"></el-button>
        <el-button type="primary" size="mini" @click="sjhf"></el-button>
      </el-col>
      <el-col :span="6">
        <el-button type="primary" size="mini" @click="sjbds"></el-button>
        <el-button type="primary" size="mini" @click="sjxd"></el-button>
        <el-button type="primary" size="mini" @click="sjbd"></el-button>
      </el-col>
      <el-col>
        <el-input v-model="input1" placeholder=""></el-input>
        <el-input v-model="input2" placeholder=""></el-input>
      </el-col>
    </el-row>
    <!--<div id="test"></div>-->
  </section>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        input1: '',//
        input2: '',//
        specimen: [
          {
            l: '',
            v: ''
          }, {
            l: '',
            v: ''
          }, {
            l: '',
            v: ''
          }
        ],
        sfarr: [
          {
            w1: '',
            w2: '',
            w3: '',
            w4: '',
            h: ''
          }, {
            w1: '',
            w2: '',
            w3: '',
            w4: '',
            h: ''
          }],
        hfarr: [
          {
            m0: '',
            m1: '',
            m2: '',
            x: ''
          }, {
            m0: '',
            m1: '',
            m2: '',
            x: ''
          }],
        bdarr: [{
          c: '',
          x: '',
        }, {
          c: '',
          x: '',
        }],
        xdarr: [
          {
            m0: '',
            m1: '',
            x: ''
          }, {
            m0: '',
            m1: '',
            x: ''
          }
        ],
        bdsarr: [
          {
            x: ''
          }, {
            x: ''
          }],
        bdsarr1: '',
        xdarr1: '',
        bdarr1: '',
        sfarr1: '',
        hfarr1: '',
        specimen_value: '',// 
        size_type: '',//
        level_name: '',// 
        jyyj: '',//
        date_val: '',// 
        jhl: [],//
        jhl_average: '',//
        htmlTitle: 'PDF'
      }
    },
    methods: {
      // 
      specimen_change() {
        if (this.specimen_value == '') {
          this.level_name = ''
        } else {
          this.level_name = ''
        }
        if (this.specimen_value == '') {
          this.jyyj = 'GB/T 8885'
        } else {
          this.jyyj = 'GB 31637'
        }
      },
      math_rander(min, max, time_nub, length) {
        // console.log('aaa', min, max, time_nub, length)
        if (min > max) {
          let a = min
          min = max
          max = a
        }
        let arr = []
        if (!time_nub) {
          let NUB = Math.random()
          if (length) {
            NUB = Number(NUB.toString().substring(0, (NUB.toString().indexOf('.') + length)))
          }
          return NUB
        } else {
          if (time_nub > 0) {
            for (let i = 0; i < time_nub; i++) {

              var Range = max - min;
              var Rand = Math.random();
              let NUB = Rand * Range
              var num = min + Number(NUB.toString().substring(0, (NUB.toString().indexOf('.') + length)))
              arr.push(num)
            }
          }
          return arr
        }
      },
      jhl_C() {
        this.other_data()
        if (!this.size_type) {
          this.$message({
            type: 'info',
            message: ''
          })
          return
        }
        let ST = Number(this.size_type)
        let sum = 0
        let randem_arr = this.math_rander(3, 6, 5, 2)
        if (randem_arr.length > 0) {
          for (let i = 0; i < randem_arr.length; i++) {
            randem_arr[i] = Number(randem_arr[i]) + ST
          }
          for (let j = 0; j < randem_arr.length; j++) {
            if (Number(randem_arr[j]).toString() != 'NaN') {
              sum += Number(randem_arr[j])
            }
          }
          this.jhl_average = ((sum / randem_arr.length) - 0.3).toFixed(1)
        }
        this.jhl = randem_arr
      },
      other_data() {
        let w1 = this.math_rander(21, 25, 1, 5)[0]
        let w2 = this.math_rander(4, 6, 1, 5)[0]
        let w3 = (this.math_rander(1, 2, 1, 5) / 1000).toFixed(5)
        let H;
        if (this.specimen_value == '') {
          H = this.math_rander(16.5, 19, 1, 2)[0]
        } else {
          H = this.math_rander(12.5, 14, 1, 2)[0]
        }
        let w4 = w2 - ((H / 100) * (w2 - w1))
        // console.log(
        //   'w1=', w1,
        //   'w2=', w2,
        //   'w3=', w3,
        //   'H=', H,
        //   'w4=', w4
        // )
      },
      // 
      meth_rander(min, max, l) {
        let cha = max - min
        let rander = Math.random()
        let qujian = (cha * rander).toFixed(l)
        return Number(min) + Number(qujian)
      },
      //
      sjsf() {
        let arr = []
        for (let i = 0; i < 2; i++) {
          let hh, w4, w3, w1, w2
          w1 = Number(this.meth_rander(21, 25, 4))
          w2 = Number(w1) + Number(this.meth_rander(4, 6, 4))
          if (this.specimen_value == '') {
            hh = Number(this.meth_rander(16.5, 19, 1))
          } else {
            hh = Number(this.meth_rander(12.5, 14, 1))
          }
          w4 = Number((w2 - (hh / 100 * (w2 - w1))).toFixed(4))
          // w4=w4
          if (this.meth_rander(0, 10, 1) > 5) {
            w3 = Number(Number(w4) + Number(0.002))
          } else {
            w3 = Number(Number(w4) + Number(0.001))
          }
          w1 = w1.toFixed(4)
          w2 = w2.toFixed(4)
          w3 = w3.toFixed(4)
          w4 = w4.toFixed(4)
          hh = hh.toFixed(1)
          arr.push({w1: w1, w2: w2, w3: w3, w4: w4, h: hh})
        }

        this.sfarr = arr
        //
        this.sfarr1 = ((Number(arr[0].h) + Number(arr[1].h)) / 2).toFixed(1)
      },

      //
      sjhf() {
        let arr = []
        for (let i = 0; i < 2; i++) {
          let x, m3, m2, m1, m0
          m0 = Number(this.meth_rander(45, 47, 4))
          m1 = Number(m0) + Number(this.meth_rander(7, 9, 4))
          console.log(',,,', m0, m1, this.meth_rander(7, 9, 4))
          if (this.specimen_value == '') {
            x = Number(this.meth_rander(11, 14, 2)) / 100
          } else {
            x = Number(this.meth_rander(22, 38, 2)) / 100
          }
          console.log('ssss', x, m1, m0)
          m2 = Number((x / 100 * (m1 - m0) + m0).toFixed(4))

          m0 = m0.toFixed(4)
          m1 = m1.toFixed(4)
          m2 = m2.toFixed(4)
          x = x.toFixed(2)
          arr.push({m0: m0, m1: m1, m2: m2, x: x})
        }

        this.hfarr = arr
        //
        // this.hfarr1 = ((
        //   Number(arr[0].m0) +
        //   Number(arr[0].m1) +
        //   Number(arr[0].m2) +
        //   Number(arr[1].m0) +
        //   Number(arr[1].m1) +
        //   Number(arr[1].m2)) / 6).toFixed(4)
        this.hfarr1 = ((Number(arr[0].x) + Number(arr[1].x)) / 2).toFixed(2)
      },

      //
      sjbds() {
        let arr = []
        for (let i = 0; i < 2; i++) {
          let c, x
          if (this.specimen_value == '')
            c = Number(this.meth_rander(5, 7, 0))
          if (this.specimen_value == '')
            c = Number(this.meth_rander(22, 28, 0))
          if (this.specimen_value == '')
            c = Number(this.meth_rander(48, 58, 0))
          x = c / 10
          arr.push({c: c, x: x})
        }

        this.bdarr = arr
        this.bdarr1 = ((Number(arr[0].x) + Number(arr[1].x)) / 2).toFixed(2)
      },

      //
      sjxd() {
        let arr = []
        for (let i = 0; i < 2; i++) {
          let x, m0, m1
          m0 = Number(this.meth_rander(48, 53, 1))
          if (this.specimen_value == '')
            x = Number(this.meth_rander(99.0, 99.8, 1))
          if (this.specimen_value == '')
            x = Number(this.meth_rander(98.0, 99.1, 1))
          if (this.specimen_value == '')
            x = Number(this.meth_rander(99.5, 99.9, 1))
          m1 = Number(x.toFixed(1)) / 100 * m0
          x = x.toFixed(1)
          m1 = m1.toFixed(1)
          arr.push({m0: m0, m1: m1, x: x})
        }

        this.xdarr = arr
        this.xdarr1 = ((Number(arr[0].x) + Number(arr[1].x)) / 2).toFixed(1)
      },

      //
      sjbd() {
        let arr = []
        for (let i = 0; i < 2; i++) {
          let x
          if (this.specimen_value == '')
            x = Number(this.meth_rander(87, 89, 1))
          if (this.specimen_value == '')
            x = Number(this.meth_rander(91, 92.5, 1))
          if (this.specimen_value == '')
            x = Number(this.meth_rander(90, 91.5, 1))
          x = x.toFixed(1)
          arr.push({x: x})
        }

        this.bdsarr = arr
        this.bdsarr1 = ((Number(arr[0].x) + Number(arr[1].x)) / 2).toFixed(2)
      },
    },
    mounted() {
    }
  }
</script>

